.banner {
  background: #000 url(//cssanimation.rocks/demo/starwars/images/bg.jpg) !important;
  height: 370px;
  margin: 0 !important;
  padding: 0 !important;
  color: white;
  font-size: 18px;
  vertical-align: top;
  display: inline-block;
  text-align: center;
  
}

@media (min-width: 576px) {
  .banner {
    font-size: calc(10px + 14 * (100vw - 320px) / 800);
  }
}



